import React, { useState } from 'react';
import iQuestionChoice from '../../../../interface/question-option.interface';
import useBEM from '../../../../utils/hooks/useBEM';
import ReactHtmlParser from 'react-html-parser';
import Icon from '../../../../components/ui/Icon';

interface Props {
    options?: iQuestionChoice[]
}

const ChoiceBox : React.FC<Props>  = ({
    options = []
}) =>  {
    // VARIABLES
    const  [B, E] = useBEM('choice-box');
    const [selected, setSelected] = useState<iQuestionChoice>();

    // HOOKS

    // METHODS
    const select = (choice: iQuestionChoice) => {
        setSelected(choice);
    }

    const selectedChoiceClassName = (choice: iQuestionChoice) => {
        return [selected?.value === choice.value ? 'selected' : '']
    }

    const renderIconName = (choice: iQuestionChoice) => {
        return selected?.value === choice.value ? `${choice.icon}-white` : `${choice.icon}`
    }

    return (
        <ul className={`${B()} ${options[0].icon ? 'choice-box-icon' : ''}`}>
            {
                options.map(option => (
                    <li 
                        key={option.value}
                        className={E('item', selectedChoiceClassName(option))}                     
                        onClick={() => select(option)}>
                        
                        {
                            option.icon && 
                            <Icon type='png' 
                            name={renderIconName(option)}
                            className={E('choice')}/>
                        }

                        {ReactHtmlParser(option.label)}

                    </li>
                ))
            }            
        </ul>
    )
}

export default ChoiceBox;