import React, { useState } from 'react';
import Icon from '../../../../components/ui/Icon';
import iQuestionChoice from '../../../../interface/question-option.interface';
import useBEM from '../../../../utils/hooks/useBEM';

interface Props {
    options?: iQuestionChoice[]
}

const ChoiceList : React.FC<Props>  = ({
    options = []
}) =>  {
    // VARIABLES
    const  [B, E] = useBEM('choice-list');
    const [selected, setSelected] = useState<iQuestionChoice>();

    // HOOKS

    // METHODS
    const select = (option: iQuestionChoice) => {
        setSelected(option);
    }

    const selectedChoiceClassName = (option: iQuestionChoice) => {
        return [selected?.value === option.value ? 'selected' : '']
    }

    const renderIconName = (choice: iQuestionChoice) => {
        if(choice.icon){
            return selected?.value === choice.value ? `${choice.icon}-white` : `${choice.icon}`
        }
        else if(choice.simpleIcon){
            return selected?.value === choice.value ? `${choice.simpleIcon}-white` : `${choice.simpleIcon}`
        }
        else{
            return ''
        }
    }

    const renderChoiceListType = () => {
        return options[0].icon ? 'choice-list-icon' : 
               options[0].simpleIcon ? 'choice-list-icon-simple' : ''
    }

    return (
        <ul className={`${B()} ${renderChoiceListType()}`}>
            {
                options.map(option => (
                    <li 
                        key={option.value}
                        className={E('item', selectedChoiceClassName(option))}                     
                        onClick={() => select(option)}>

                        {
                            option.icon || option.simpleIcon ?
                            <Icon type='png' 
                            name={renderIconName(option)}
                            className={E('choice')}/> : null
                        }

                        {option.label}
                    </li>
                ))
            }            
        </ul>
    )
}

export default ChoiceList;